






import Vue from 'vue';
import ScheduleStepFilter from '@/components/ScheduleAddModule/ScheduleStepFilter.vue';

export default Vue.extend({
  components: {
    ScheduleStepFilter,
  },
  data:() => ({
    menuWidth: 0,
    menuType: 'select',
  }),
  watch: {
    $route: {
      handler(route: any): void {
        const queries = route.query;
        const queriesPath = route.fullPath.replace(route.path, '');
        this.setPageData(queries, queriesPath, this.menuType);
      },
      deep: true,
      immediate: true,
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.menuWidth = this.$el.clientWidth;
    });
  },
  methods: {
    setPageData(queries: any, queriesPath: any, menuType: string) {
      this.$emit('setPageData', queries, queriesPath, menuType);
    },
    queriesIsNotEmpty(obj: any) {
      return Object.keys(obj).length;
    },
  },
});
